'use client'

import { useState } from 'react'
import { useForm } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import * as z from 'zod'
import {
  Card,
  CardHeader,
  CardTitle,
  CardContent,
  Table,
  TableHeader,
  TableRow,
  TableHead,
  TableBody,
  TableCell,
  Button,
  Input,
  Label,
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
  Calendar,
  Popover,
  PopoverContent,
  PopoverTrigger,
  Badge
} from '@/components/ui'
import { CalendarIcon } from '@radix-ui/react-icons'
import { format } from 'date-fns'
import { cn } from '@/lib/utils'
import { toast } from 'sonner'

// نموذج التحقق
const formSchema = z.object({
  اسم_المستخدم: z.string().min(1, 'مطلوب'),
  كلمة_السر: z.string().min(6, 'يجب أن تكون كلمة السر 6 أحرف على الأقل'),
  الاسم_العائلي: z.string().min(1, 'مطلوب'),
  الاسم_الشخصي: z.string().min(1, 'مطلوب'),
  الهاتف: z.string().optional(),
  البريد_الإلكتروني: z.string().email('بريد إلكتروني غير صالح').optional(),
  تاريخ_التوظيف: z.date(),
  تاريخ_الالتحاق: z.date(),
  الإطار: z.string().min(1, 'مطلوب'),
  الصنف: z.string().min(1, 'مطلوب'),
})

export default function إدارة_المستخدمين() {
  const [المستخدمون, setالمستخدمون] = useState([
    {
      معرف: 1,
      الاسم_الشخصي: 'محمد',
      الاسم_العائلي: 'الفضيل',
      البريد_الإلكتروني: 'm.alfadil@example.com',
      الهاتف: '0612345678',
      الإطار: 'مشرف',
      الصنف: 'مستند قضائي من الدرجة 2',
      تاريخ_التوظيف: new Date('2020-01-15'),
      تاريخ_الالتحاق: new Date('2020-02-01')
    }
  ])

  const { register, handleSubmit, setValue, formState: { errors } } = useForm({
    resolver: zodResolver(formSchema)
  })

  const onSubmit = (data) => {
    const مستخدم_جديد = {
      معرف: المستخدمون.length + 1,
      ...data
    }
    setالمستخدمون([...المستخدمون, مستخدم_جديد])
    toast.success('تمت إضافة المستخدم بنجاح')
  }

  return (
    <div className="container mx-auto py-8" dir="rtl">
      <Card>
        <CardHeader>
          <CardTitle className="text-2xl font-bold">إدارة المستخدمين</CardTitle>
        </CardHeader>

        <CardContent>
          <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              
              {/* المعلومات الشخصية */}
              <div className="space-y-4">
                <h3 className="text-lg font-semibold">المعلومات الشخصية</h3>
                
                <div className="space-y-2">
                  <Label htmlFor="اسم_المستخدم">اسم المستخدم</Label>
                  <Input id="اسم_المستخدم" {...register('اسم_المستخدم')} />
                  {errors.اسم_المستخدم && <p className="text-red-500 text-sm">{errors.اسم_المستخدم.message}</p>}
                </div>

                <div className="space-y-2">
                  <Label htmlFor="كلمة_السر">كلمة السر</Label>
                  <Input id="كلمة_السر" type="password" {...register('كلمة_السر')} />
                  {errors.كلمة_السر && <p className="text-red-500 text-sm">{errors.كلمة_السر.message}</p>}
                </div>

                <div className="space-y-2">
                  <Label htmlFor="الاسم_العائلي">الاسم العائلي</Label>
                  <Input id="الاسم_العائلي" {...register('الاسم_العائلي')} />
                  {errors.الاسم_العائلي && <p className="text-red-500 text-sm">{errors.الاسم_العائلي.message}</p>}
                </div>

                <div className="space-y-2">
                  <Label htmlFor="الاسم_الشخصي">الاسم الشخصي</Label>
                  <Input id="الاسم_الشخصي" {...register('الاسم_الشخصي')} />
                  {errors.الاسم_الشخصي && <p className="text-red-500 text-sm">{errors.الاسم_الشخصي.message}</p>}
                </div>
              </div>

              {/* معلومات الاتصال */}
              <div className="space-y-4">
                <h3 className="text-lg font-semibold">معلومات الاتصال</h3>
                
                <div className="space-y-2">
                  <Label htmlFor="الهاتف">الهاتف</Label>
                  <Input id="الهاتف" {...register('الهاتف')} />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="البريد_الإلكتروني">البريد الإلكتروني</Label>
                  <Input id="البريد_الإلكتروني" type="email" {...register('البريد_الإلكتروني')} />
                  {errors.البريد_الإلكتروني && <p className="text-red-500 text-sm">{errors.البريد_الإلكتروني.message}</p>}
                </div>

                <div className="space-y-2">
                  <Label>تاريخ التوظيف</Label>
                  <Popover>
                    <PopoverTrigger asChild>
                      <Button
                        variant="outline"
                        className={cn("w-full justify-start text-right font-normal")}
                      >
                        <CalendarIcon className="ml-2 h-4 w-4" />
                        اختر تاريخًا
                      </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0">
                      <Calendar
                        mode="single"
                        onSelect={(date) => setValue('تاريخ_التوظيف', date)}
                        initialFocus
                      />
                    </PopoverContent>
                  </Popover>
                </div>

                <div className="space-y-2">
                  <Label>تاريخ الالتحاق</Label>
                  <Popover>
                    <PopoverTrigger asChild>
                      <Button
                        variant="outline"
                        className={cn("w-full justify-start text-right font-normal")}
                      >
                        <CalendarIcon className="ml-2 h-4 w-4" />
                        اختر تاريخًا
                      </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0">
                      <Calendar
                        mode="single"
                        onSelect={(date) => setValue('تاريخ_الالتحاق', date)}
                        initialFocus
                      />
                    </PopoverContent>
                  </Popover>
                </div>
              </div>

              {/* الإطار والصنف */}
              <div className="space-y-4">
                <h3 className="text-lg font-semibold">الوظيفة</h3>
                
                <div className="space-y-2">
                  <Label htmlFor="الإطار">الإطار</Label>
                  <Select onValueChange={(value) => setValue('الإطار', value)}>
                    <SelectTrigger>
                      <SelectValue placeholder="اختر الإطار" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="موظف">موظف</SelectItem>
                      <SelectItem value="مشرف">مشرف</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="الصنف">الصنف</Label>
                  <Select onValueChange={(value) => setValue('الصنف', value)}>
                    <SelectTrigger>
                      <SelectValue placeholder="اختر الصنف" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="مستند قضائي من الدرجة 2">مستند قضائي من الدرجة 2</SelectItem>
                      <SelectItem value="صنف آخر">صنف آخر</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>
            </div>

            <div className="flex gap-4 justify-end">
              <Button type="button" variant="outline">
                مسح
              </Button>
              <Button type="submit">
                إضافة جديد
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>

      {/* جدول المستخدمين */}
      <Card className="mt-8">
        <CardHeader>
          <CardTitle>قائمة المستخدمين</CardTitle>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>الاسم الكامل</TableHead>
                <TableHead>البريد الإلكتروني</TableHead>
                <TableHead>الهاتف</TableHead>
                <TableHead>الإطار</TableHead>
                <TableHead>الصنف</TableHead>
                <TableHead>تاريخ التوظيف</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {المستخدمون.map((user) => (
                <TableRow key={user.معرف}>
                  <TableCell>{user.الاسم_الشخصي} {user.الاسم_العائلي}</TableCell>
                  <TableCell>{user.البريد_الإلكتروني}</TableCell>
                  <TableCell>{user.الهاتف}</TableCell>
                  <TableCell>
                    <Badge variant={user.الإطار === 'مشرف' ? 'default' : 'secondary'}>
                      {user.الإطار}
                    </Badge>
                  </TableCell>
                  <TableCell>{user.الصنف}</TableCell>
                  <TableCell>{format(user.تاريخ_التوظيف, 'yyyy/MM/dd')}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  )
}